.image2{
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
  margin-bottom: 30px;

}


.background-image{
  background: url('../images/bg-login.jpg') ;
  background-size:cover;
  background-repeat: no-repeat;
  font-family:Georgia;
  height: 100vh;
  padding-top: 60px;
 
}


.login-block {
  width: 320px;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  border-top: 5px solid #28206e;
  margin: auto auto;
}

.login-block h1 {
  text-align: center;
  color: #000;
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 20px;
}

.login-block input {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  border-radius: 30px;
  border: 1px solid #ccc;
  margin-bottom: 20px;
  font-size: 16px;
  font-family:'Times New Roman', Times, serif;
  padding: 0 50px 0 50px;
  outline: none;
}

.login-block input#identifiant {
  background: #fff url('../images/user.png');
  background-repeat: no-repeat;
  background-size: 10%;
  background-position:5% ;
}


.login-block input#password {
  background: #fff url('../images/lock.png') ;
  background-repeat: no-repeat;
  background-size: 10%;
  background-position: 5% ;


}


.login-block input:active, .login-block input:focus {
  border: 1px solid #4a55b9;
}

.login-block button {
  width: 100%;
  height: 40px;
  background: #5373cc;
  box-sizing: border-box;
  border-radius: 30px;
  border: 1px solid #8078e4;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 14px;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  outline: none;
  cursor: pointer;
}

.login-block button:hover {
  background: #7062f0;
}
